"use client"

import { useState } from "react"
import { useRouter } from "next/navigation"
import Link from "next/link"
import { Button } from "@/components/ui/button"
import { Card, CardContent } from "@/components/ui/card"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { Textarea } from "@/components/ui/textarea"
import { Slider } from "@/components/ui/slider"
import { Badge } from "@/components/ui/badge"
import {
  AlertDialog,
  AlertDialogAction,
  AlertDialogCancel,
  AlertDialogContent,
  AlertDialogDescription,
  AlertDialogFooter,
  AlertDialogHeader,
  AlertDialogTitle,
  AlertDialogTrigger,
} from "@/components/ui/alert-dialog"
import { ArrowLeft, Star, Pencil, Trash2 } from "lucide-react"

const FORMALITY_COLORS = [
  "bg-[oklch(var(--formality-1))]",
  "bg-[oklch(var(--formality-2))]",
  "bg-[oklch(var(--formality-3))]",
  "bg-[oklch(var(--formality-4))]",
  "bg-[oklch(var(--formality-5))]",
]

const FORMALITY_LABELS = ["Home", "Neighborhood", "Outing", "Work", "Formal"]

// Mock data - in real app, would be fetched based on ID
const MOCK_OUTFIT = {
  id: 1,
  name: "Office Casual",
  rating: 5,
  formalityLevel: 4,
  memo: "Perfect for client meetings",
  wornCount: 12,
  lastWornDate: "2025-01-08",
  items: [
    { id: 1, category: "Tops", name: "White Oxford Shirt", image: "/white-oxford-shirt.png" },
    { id: 2, category: "Outerwear", name: "Navy Blazer", image: "/navy-blazer.png" },
    { id: 3, category: "Bottoms", name: "Black Trousers", image: "/black-trousers.png" },
  ],
}

export default function OutfitDetailPage({ params }: { params: { id: string } }) {
  const router = useRouter()
  const [isEditing, setIsEditing] = useState(false)
  const [formData, setFormData] = useState({
    name: MOCK_OUTFIT.name,
    rating: MOCK_OUTFIT.rating,
    formalityLevel: [MOCK_OUTFIT.formalityLevel],
    memo: MOCK_OUTFIT.memo,
  })

  const handleSave = () => {
    // TODO: Implement API call to update outfit
    console.log("Updating outfit:", params.id, formData)
    setIsEditing(false)
  }

  const handleDelete = () => {
    // TODO: Implement API call to delete outfit
    console.log("Deleting outfit:", params.id)
    router.push("/outfits")
  }

  const handleCancel = () => {
    // Reset form data
    setFormData({
      name: MOCK_OUTFIT.name,
      rating: MOCK_OUTFIT.rating,
      formalityLevel: [MOCK_OUTFIT.formalityLevel],
      memo: MOCK_OUTFIT.memo,
    })
    setIsEditing(false)
  }

  return (
    <div className="min-h-screen bg-background">
      <header className="border-b border-border/50 bg-background/80 backdrop-blur-md sticky top-0 z-10">
        <div className="container mx-auto px-6 py-6">
          <div className="flex items-center justify-between">
            <div className="flex items-center gap-4">
              <Button variant="ghost" size="icon" asChild>
                <Link href="/outfits">
                  <ArrowLeft className="w-4 h-4" />
                </Link>
              </Button>
              <h1 className="text-xl font-serif tracking-tight">Outfit Details</h1>
            </div>
            <div className="flex gap-2">
              {!isEditing ? (
                <>
                  <Button variant="outline" size="sm" onClick={() => setIsEditing(true)}>
                    <Pencil className="w-4 h-4 mr-2" />
                    Edit
                  </Button>
                  <AlertDialog>
                    <AlertDialogTrigger asChild>
                      <Button variant="destructive" size="sm">
                        <Trash2 className="w-4 h-4 mr-2" />
                        Delete
                      </Button>
                    </AlertDialogTrigger>
                    <AlertDialogContent>
                      <AlertDialogHeader>
                        <AlertDialogTitle>Are you sure?</AlertDialogTitle>
                        <AlertDialogDescription>
                          This action cannot be undone. This will permanently delete this outfit from your collection.
                        </AlertDialogDescription>
                      </AlertDialogHeader>
                      <AlertDialogFooter>
                        <AlertDialogCancel>Cancel</AlertDialogCancel>
                        <AlertDialogAction onClick={handleDelete} className="bg-destructive text-destructive-foreground">
                          Delete
                        </AlertDialogAction>
                      </AlertDialogFooter>
                    </AlertDialogContent>
                  </AlertDialog>
                </>
              ) : (
                <>
                  <Button variant="outline" size="sm" onClick={handleCancel}>
                    Cancel
                  </Button>
                  <Button size="sm" onClick={handleSave}>
                    Save Changes
                  </Button>
                </>
              )}
            </div>
          </div>
        </div>
      </header>

      <main className="container mx-auto px-6 py-12 max-w-4xl">
        <div className="grid md:grid-cols-2 gap-8">
          {/* Left: Outfit Items */}
          <Card className="border border-border/50">
            <CardContent className="p-6">
              <h2 className="text-lg font-serif font-light mb-6">Items in this Outfit</h2>
              <div className="grid grid-cols-2 gap-4">
                {MOCK_OUTFIT.items.map((item) => (
                  <div key={item.id} className="space-y-2">
                    <Badge variant="secondary" className="text-xs font-normal">
                      {item.category}
                    </Badge>
                    <div className="aspect-square overflow-hidden rounded-lg bg-muted/30 border border-border/50">
                      <img src={item.image || "/placeholder.svg"} alt={item.name} className="w-full h-full object-cover" />
                    </div>
                    <p className="text-xs font-light text-center">{item.name}</p>
                  </div>
                ))}
              </div>

              {/* Stats */}
              <div className="mt-8 pt-6 border-t border-border/50 space-y-4">
                <div className="flex justify-between items-center">
                  <span className="text-sm text-muted-foreground font-light">Worn Count</span>
                  <span className="text-lg font-light">{MOCK_OUTFIT.wornCount}x</span>
                </div>
                <div className="flex justify-between items-center">
                  <span className="text-sm text-muted-foreground font-light">Last Worn</span>
                  <span className="text-sm font-light">{new Date(MOCK_OUTFIT.lastWornDate).toLocaleDateString()}</span>
                </div>
              </div>
            </CardContent>
          </Card>

          {/* Right: Outfit Details */}
          <div className="space-y-6">
            <Card className="border border-border/50">
              <CardContent className="p-6 space-y-6">
                {/* Outfit Name */}
                <div className="space-y-3">
                  <Label className="text-sm font-light text-muted-foreground uppercase tracking-wider">
                    Outfit Name
                  </Label>
                  {!isEditing ? (
                    <p className="text-xl font-serif font-light">{formData.name || "Untitled Outfit"}</p>
                  ) : (
                    <Input
                      placeholder="e.g., Office Casual"
                      className="border-border/50"
                      value={formData.name}
                      onChange={(e) => setFormData({ ...formData, name: e.target.value })}
                    />
                  )}
                </div>

                {/* Rating */}
                <div className="space-y-3">
                  <Label className="text-sm font-light text-muted-foreground uppercase tracking-wider">
                    Rating
                  </Label>
                  <div className="flex items-center gap-2">
                    {[1, 2, 3, 4, 5].map((value) => (
                      <button
                        key={value}
                        onClick={() => isEditing && setFormData({ ...formData, rating: value })}
                        className={`transition-colors ${!isEditing ? "cursor-default" : ""}`}
                        type="button"
                        disabled={!isEditing}
                      >
                        <Star
                          className={`w-8 h-8 ${
                            value <= formData.rating ? "fill-primary text-primary" : "text-muted-foreground/30"
                          }`}
                        />
                      </button>
                    ))}
                  </div>
                </div>

                {/* Formality Level */}
                <div className="space-y-4">
                  <div className="flex items-center justify-between">
                    <Label className="text-sm font-light text-muted-foreground uppercase tracking-wider">
                      Formality Level
                    </Label>
                    <div className="flex items-center gap-2">
                      <div className={`w-3 h-3 rounded-full ${FORMALITY_COLORS[formData.formalityLevel[0] - 1]}`} />
                      <Badge variant="outline" className="border-border/50">
                        {formData.formalityLevel[0]} - {FORMALITY_LABELS[formData.formalityLevel[0] - 1]}
                      </Badge>
                    </div>
                  </div>
                  {isEditing && (
                    <>
                      <Slider
                        value={formData.formalityLevel}
                        onValueChange={(value) => setFormData({ ...formData, formalityLevel: value })}
                        min={1}
                        max={5}
                        step={1}
                        className="py-4"
                      />
                      <div className="flex justify-between text-xs text-muted-foreground">
                        <span>1 - Home</span>
                        <span>5 - Formal</span>
                      </div>
                    </>
                  )}
                </div>

                {/* Memo */}
                <div className="space-y-3">
                  <Label className="text-sm font-light text-muted-foreground uppercase tracking-wider">
                    Memo
                  </Label>
                  {!isEditing ? (
                    <p className="text-sm font-light text-muted-foreground">
                      {formData.memo || "No memo added"}
                    </p>
                  ) : (
                    <Textarea
                      placeholder="e.g., For important meetings"
                      className="border-border/50 min-h-24"
                      value={formData.memo}
                      onChange={(e) => setFormData({ ...formData, memo: e.target.value })}
                    />
                  )}
                </div>
              </CardContent>
            </Card>

            {/* Wear Today Button */}
            {!isEditing && (
              <Button className="w-full" size="lg">
                Wear This Outfit Today
              </Button>
            )}
          </div>
        </div>
      </main>
    </div>
  )
}
